@import "../../assets/style/app";

.firstScreen {
  @include container;
  height: 573px;
  background-image: url(../../assets/gif/firstScreen_mobile.gif);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  @include desktop {
    padding-top: 10px;
    height: 810px;
    background-image: url(../../assets/gif/firstScreen_desktop.gif);
  }

  .inner {
    @include inner;

    .title {
      margin-left: auto;
      margin-right: auto;
      width: 355px;
      max-width: 100%;

      @include desktop {
        margin-left: 0;
        width: 703px;
      }

      img {
        display: block;
        width: 100%;
        height: auto;

        @include desktop {
          width: 703px;
        }
      }
    }

    .description {
      @include onlyDesktop() {
        @include TitanOne;
        @include text_shadow_black10;
        font-weight: 400;
        font-size: 31px;
        line-height: 108%;
        color: #9dffca;
        width: 567px;
        padding-left: 40px;
      }
    }
  }
}